.home {
  display: grid;
  padding: 2rem;
  grid-gap: 2px;
  height: 80vh;
  background-repeat: no-repeat;
  background-size: cover;
  grid-template-columns: 1fr 1fr;
  /* background-image: url(../../Images//banner-01.png); */
}
.button-50 {
  appearance: button;
  background-color: #000;
  background-image: none;
  border: 1px solid #000;
  border-radius: 4px;
  box-shadow: #fff 4px 4px 0 0, #000 4px 4px 0 1px;
  box-sizing: border-box;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  font-family: ITCAvantGardeStd-Bk, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  margin: 0 5px 10px 0;
  overflow: visible;
  padding: 12px 20px;
  text-align: center;
  text-transform: none;
  touch-action: manipulation;
  user-select: none;
  -webkit-user-select: none;
  vertical-align: middle;
  white-space: nowrap;
}

.button-50:focus {
  text-decoration: none;
}

.button-50:hover {
  text-decoration: none;
}

.button-50:active {
  box-shadow: rgba(0, 0, 0, 0.125) 0 3px 5px inset;
  outline: 0;
}

.button-50:not([disabled]):active {
  box-shadow: #fff 2px 2px 0 0, #000 2px 2px 0 1px;
  transform: translate(2px, 2px);
}

.home_left {
  padding: 5rem;
}

.heading {
  text-transform: capitalize;
}

.greeting {
  font-size: 50px;
  margin-bottom: 1rem;
}
.para1 {
  padding: 1rem;
  font-size: 15px;
  margin-bottom: 1rem;
}
.para2 {
  font-size: 17px;
  margin-bottom: 1rem;
}

.buttons {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: flex-start;
}
.imagessection {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 1rem;
}
.imagessection > img {
  border-radius: 1rem;
}

@media (max-width: 600px) {
  .home_left {
    padding: 0.2rem;
  }
  .greeting {
    font-size: 25px;
  }
  .button-50 {
    padding: 7px 18px;
    font-size: 10px;
  }
  .greeting2 {
    font-size: 12px;
  }
  .home_right {
    display: none;
  }
  .home {
    grid-template-columns: 1fr;
  }
}



.homeimgbox{
  transition: 1s ease-in-out;
  overflow: hidden;
  border-radius: 5px;
}
.homeimgs{
  transition: .5s;
  border-radius: 5px;
  cursor: pointer;
}
.homeimgs:hover{
  transform: scale(1.1);
  border-radius: 5px;
}