.ai_generator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.3rem;
}
.ai_heading {
  font-size: 50px;
}
/* .ai_input_form {
  margin-bottom: 1rem;
} */
.ai_input {
  border-radius: 0.6rem;
  padding: 0.3rem 3rem;
  height: 4rem;
  background-color: transparent;
  width: 53rem;
  border: 0px solid;
  font-size: 21px;
  color: rgb(22, 22, 22);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.button-85 {
  margin-left: 0.6rem;
  padding: 1.8em 3.5em;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-85:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing-button-85 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

@keyframes glowing-button-85 {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.button-85:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #222;
  left: 0;
  top: 0;
  border-radius: 10px;
}

.navigations {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  margin-top: 1rem;
  justify-content: space-evenly;
}
.loading {
  transition: 3s;
}
.loading-bar {
  width: 0px;
  height: 3px;
  background: red;
  border-radius: 12px;
}
.loading-tex {
  font-size: 18px;
}

.loading-bar-full {
  width: 100vw;
  height: 8px;
  background: rgb(151, 226, 179);
  transition: 18s;
  border-radius: 12px;
}

.display-none {
  display: none;
}

.image_gllary {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 1rem 3rem;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}
.images {
  overflow: hidden;
  transition: 0.4s;
  border-radius: 5px;
}
.img {
  transition: 0.4s;
}

.img:hover {
  opacity: 0.8;
  transform: scale(1.1);
}

.button-33 {
  background-color: #c2fbd7;
  border-radius: 7px;
  box-shadow: rgba(44, 187, 99, 0.2) 0 -25px 18px -14px inset,
    rgba(44, 187, 99, 0.15) 0 1px 2px, rgba(44, 187, 99, 0.15) 0 2px 4px,
    rgba(44, 187, 99, 0.15) 0 4px 8px, rgba(44, 187, 99, 0.15) 0 8px 16px,
    rgba(44, 187, 99, 0.15) 0 16px 32px;
  color: green;
  cursor: pointer;
  display: inline-block;
  font-family: CerebriSans-Regular, -apple-system, system-ui, Roboto, sans-serif;
  padding: 7px 20px;
  text-align: center;
  text-decoration: none;
  transition: all 250ms;
  border: 0;
  font-size: 16px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-33:hover {
  box-shadow: rgba(44, 187, 99, 0.35) 0 -25px 18px -14px inset,
    rgba(44, 187, 99, 0.25) 0 1px 2px, rgba(44, 187, 99, 0.25) 0 2px 4px,
    rgba(44, 187, 99, 0.25) 0 4px 8px, rgba(44, 187, 99, 0.25) 0 8px 16px,
    rgba(44, 187, 99, 0.25) 0 16px 32px;
  transform: scale(1.05) rotate(-1deg);
}

.selection {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.pagination {
  display: flex;
  justify-content: center;
}
.defaultImage {
  height: 300px;
}
